body {
  padding-top: 100px;
}
body.admin-bar {
  padding-top: 100px;
}
body.hide-header {
  padding-top: 0;
}
#wpadminbar {
  position: fixed;
  top: 0;
}
.admin-bar .navbar {
  top: 46px;
}
.navbar-brand {
  float: left;
  padding: 32px 15px;
  max-width: 130px;
}
@media (min-width: 768px) {
  .navbar-brand {
    padding: 25px 0;
    max-width: 130px;
  }
}
.navbar-brand:hover {
  opacity: 0.5;
}
.navbar a {
  transition: all .3s ease;
}
.navbar-default.navbar-nav > li > a {
  color: #333;
}
@media (min-width: 768px) {
  .dropdown-menu > li > a:focus,
  .dropdown-menu > li > a:hover {
    text-decoration: none;
    color: #fff;
    background-color: #024631;
  }
  .dropdown-menu {
    min-width: 200px;
    left: 50% !important;
    right: auto !important;
    text-align: left;
    transform: translate(-50%, 0);
  }
  .admin-bar .navbar {
    top: 32px;
  }
  .navbar-nav > li > a,
  .navbar-brand,
  .navbar {
    transition: all 0.35s;
    color: #333 !important;
  }
  .navbar.navbar-on-scroll {
    min-height: 75px;
  }
  .navbar.navbar-on-scroll .navbar-brand {
    height: 75px;
    padding: 15px 0;
    max-width: 100px;
  }
  .navbar-on-scroll .navbar-nav > li > a,
  .navbar-on-scroll .navbar-brand,
  .navbar-on-scroll .navbar {
    transition: all 0.35s;
    padding-top: 26px;
    padding-bottom: 26px;
  }
}
.navbar-nav > li.current-menu-parent > a {
  color: #333;
}
.dropdown-menu {
  border: none;
  border-top: 2px solid #024631;
  padding: 0;
}
.dropdown-menu li > a {
  border-bottom: 1px solid #e5e5e5;
  padding: 10px 20px;
}
.scroll-top {
  position: fixed;
  bottom: -3em;
  right: 15px;
  text-align: center;
  height: 3em;
  width: 3em;
  background: #00373c;
  border-radius: 4px;
  transition: all .4s;
  cursor: pointer;
  color: white;
  padding: 0.8em;
  display: block;
  z-index: 0;
  opacity: 0;
}
.scroll-top.visible {
  bottom: 15px;
  z-index: 100;
  opacity: 1;
}
.scroll-top.visible:hover,
.scroll-top.visible:focus {
  opacity: 0.8;
  color: white;
}
.scroll-top i {
  color: inherit;
}
@media (min-width: 768px) {
  .scroll-top {
    bottom: -5em;
    right: 1.5em;
  }
  .scroll-top.visible {
    bottom: 1.5em;
  }
}
